$appName: pdf_downloader; $cssPrefix: pdf_downloader-;


.list-item__wrapper{
  position: relative;
  width: 100%;
}

// listItem
.list-item {
  display: block;
  position: relative;
  flex: 0 0 auto;
  justify-content: flex-start;
  padding: 8px;
  border-radius: 16px;
  margin: 2px 0;
  width: 100%;
  cursor: pointer;
  transition: background-color 200ms ease-in-out;
  list-style: none;
  &:hover,
  &:focus {
    background-color: var(--color-background-hover);
  }
  &--active,
  &:active,
  &:active ~ .app-navigation-entry__utils {
    background-color: var(--color-primary-light);
  }

  &-content__wrapper {
    display: flex;
    align-items: center;
  }

  &-content {
    display: flex;
    flex: 1 1 auto;
    justify-content: space-between;
    padding-left: 8px;

    &__main {
      flex: 1 1 auto;
      flex-direction: column;
      width: 0;
      margin: auto 0;
    }

    &__actions {
      flex: 0 0 auto;
      align-self: center;
      justify-content: center;

    }
  }

  &__extra {
    margin-top: 4px;
  }
}

.line-one {
  display: flex;
  align-items: center;
  justify-content: space-between;
  white-space: nowrap;
  margin: 0 auto;
  &--bold {
    font-weight: bold;
  }

  &__title {
    overflow: hidden;
    flex-grow: 1;
    cursor: pointer;
    text-overflow: ellipsis;
    color: var(--color-main-text);
  }

  &__details {
    color: var(--color-text-lighter);
    margin: 0 8px;
    font-weight: normal;
  }
}

.line-two {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  white-space: nowrap;
  &--bold {
    font-weight: bold;
  }

  &__subtitle {
    overflow: hidden;
    flex-grow: 1;
    padding-right: 4px;
    cursor: pointer;
    white-space: nowrap;
    text-overflow: ellipsis;
    color: var(--color-text-lighter);
  }

  &__counter {
    margin: 2px 4px 0 0;
  }
}

